<template>
  <main class="triagemContainer">
    <aside class="fila">
      <div class="listaTriagem" v-for="(item, index) in lTriagem" :key="index" @click="carregaTriagem(item)">
        <div class="ltCliente">
          <div class="cNome">
            <div class="cID">
              ID: <label class="dataLabel">{{ item.idcliente }}</label>
            </div>
            <div class="cNome2">
              Nome: <label class="dataLabel">{{ item.nome }}</label>
            </div>
          </div>
          <div class="cDocs">
            <div class="cDoc">
              CPF: <label class="dataLabel">{{ item.doc }}</label>
            </div>
            <div class="cDoc">
              N SUS: <label class="dataLabel">{{ item.docsus }}</label>
            </div>
          </div>
          <div class="cContato">
            <div class="cDoc">
              Telefone: <label class="dataLabel">{{ item.telefone }}</label>
            </div>
            <div class="cDoc">
              E-Mail: <label class="dataLabel">{{ item.email }}</label>
            </div>
          </div>
        </div>
        <div class="ltMotivo">
          <div class="cMotivo">
            <div class="cDoc">
              Tipo de atendimento: <label class="dataLabel">{{ item.idespec }}</label>
            </div>
            <div class="cDoc">
              Tipo de especialidade: <label class="dataLabel">{{ item.idsubspec }}</label>
            </div>
          </div>
        </div>
        <div class="ltSenha">
          <div :class="[
            'cSenha',
            {
              csPriority1: item.codurg === 1,
              csPriority2: item.codurg === 2,
              csPriority3: item.codurg === 3,
            },
          ]">
            <div class="sPrefix">
              {{ item.espec_prefix }}
            </div>
            <div class="sNumber">
              {{ String(item.idtriagem).padStart(3, "0") }}
            </div>
          </div>
        </div>
      </div>
    </aside>
    <div class="panelControl">
      <div v-if="triagemSelecionada" class="pcTop">
        <div class="listaTriagem ltSelecionada">
          <div class="ltCliente">
            <div class="cNome">
              <div class="cID">
                ID: <label class="dataLabel">{{ triagemSelecionada.idcliente }}</label>
              </div>
              <div class="cNome2">
                Nome: <label class="dataLabel">{{ triagemSelecionada.nome }}</label>
              </div>
            </div>
            <div class="cDocs">
              <div class="cDoc">
                CPF: <label class="dataLabel">{{ triagemSelecionada.doc }}</label>
              </div>
              <div class="cDoc">
                N SUS: <label class="dataLabel">{{ triagemSelecionada.docsus }}</label>
              </div>
            </div>
            <div class="cContato">
              <div class="cDoc">
                Telefone:
                <label class="dataLabel">{{ triagemSelecionada.telefone }}</label>
              </div>
              <div class="cDoc">
                E-Mail: <label class="dataLabel">{{ triagemSelecionada.email }}</label>
              </div>
            </div>
          </div>
          <div class="ltMotivo">
            <div class="cMotivo">
              <div class="cDoc">
                Tipo de atendimento:
                <label class="dataLabel">{{ triagemSelecionada.idespec }}</label>
              </div>
              <div class="cDoc">
                Tipo de especialidade:
                <label class="dataLabel">{{ triagemSelecionada.idsubspec }}</label>
              </div>
            </div>
          </div>
          <div class="ltSenha ltSenhaSelect">
            <div :class="[
              'cSenha',
              'cSenhaSelect',
              {
                csPriority1: triagemSelecionada.codurg === 1,
                csPriority2: triagemSelecionada.codurg === 2,
                csPriority3: triagemSelecionada.codurg === 3,
              },
            ]">
              <div class="sPrefix">
                {{ triagemSelecionada.espec_prefix }}
              </div>
              <div class="sNumber">
                {{ String(triagemSelecionada.idtriagem).padStart(3, "0") }}
              </div>
            </div>
          </div>
        </div>
        <div class="pcTopMenu">
          <div class="priorityChange">
            <div>Prioridade:</div>
            <select class="pSelect" v-model="selectedPriority" @click="sOpcao(selectedPriority)">
              <option class="pSelectOption" value="" disabled selected>
                Selecione uma prioridade
              </option>
              <option class="pSelectOption" v-for="priority in priority" :value="priority.p" :key="priority.p">
                {{ priority.name }}
              </option>
            </select>
          </div>
          <div class="salvaTriagem" @click="salvaTriagem()">Salvar</div>
          <div class="chamaTriagem" @click="chamaTriagem()">Chamar</div>
          <div class="cancelaTriagem" @click="cancelaTriagem(triagemSelecionada)">Cancelar</div>
        </div>
      </div>
      <div v-if="triagemChamada" class="pcWherever" @click="RechamaTriagem()">
        <div class="ltSenha ltSenhaSelect">
          <div :class="[
            'cSenha',
            'cSenhaSelect',
            {
              csPriority1: triagemChamada.codurg === 1,
              csPriority2: triagemChamada.codurg === 2,
              csPriority3: triagemChamada.codurg === 3,
            },
          ]"></div>
        </div>

        <div class="csChamada">
          <div class="sChamada">
            <div class="sPrefix">
              {{ triagemChamada.espec_prefix }}
            </div>
            <div class="csNumber">
              {{ String(triagemChamada.idtriagem).padStart(3, "0") }}
            </div>
          </div>
          <div class="mensagemChamada">Por favor, dirija-se ao balco {{ bLivre }}</div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import axios from "axios";

import { io } from "socket.io-client";

// const socket = io("http://localhost:3000");
const socket = io("https://vizinhoapi.herokuapp.com/");

const http = axios.create();

export default {
  data() {
    return {
      BASE_URL: null,
      lTriagem: null,
      triagemSelecionada: null,
      priority: [
        { p: 1, name: "Urgncia" },
        { p: 2, name: "Intermedirio" },
        { p: 3, name: "No urgente" },
      ],
      selectedPriority: null,
      bLivre: null,
      triagemChamada: null,
      messageContainer: null,
      ioclient: socket,
      returnServerMsg: [],
    };
  },
  mounted() {

    this.BASE_URL = "https://vizinhoapi.herokuapp.com/"
    // this.BASE_URL = "http://localhost:3000/";
    this.getTriagem();

    this.messageContainer = this.$refs.messageContainer;
    this.socketConnection();
    this.waitConnected();
    this.waitChamada();
    this.waitClientJoin();
  },
  methods: {
    RandomNumber() {
      return Math.floor(Math.random() * 10) + 1;
    },
    async cancelaTriagem(Value) {

      var idtriagem = Value.idtriagem;

      try {

        const response = await http.patch(this.BASE_URL + "triagem/delete", {
          triagem_id: idtriagem
        });

        this.cancelaTriagemCliente()

        this.$router.go(0);

      } catch (error) {
        // console.log({ error });
      }
    },
    cancelaTriagemCliente() {
      
      this.triagemChamada = this.triagemSelecionada;

      let id = this.triagemChamada.idcliente

      this.ioclient.emit("send_ClientCancel", id);

    },
    chamaTriagem() {

      let balcao = this.RandomNumber();
      this.triagemChamada = this.triagemSelecionada;
      this.bLivre = balcao;

      var triagemSenha = `${this.triagemChamada.espec_prefix}${String(this.triagemChamada.idtriagem).padStart(3, "0")}`

      this.sendAlertaCliente(this.triagemChamada.idcliente, balcao)

      if (Notification.permission === "granted") {
        const notification = new Notification("SUS Notifications", {
          body: `${triagemSenha} Sua vez chegou, por favor, dirija-se ao balco ${this.bLivre}`,
          // Outras opes de configurao da notificao
        });
      }

    },    
    RechamaTriagem() {

      let balcao = this.RandomNumber();
      this.bLivre = balcao;

      var triagemSenha = `${this.triagemChamada.espec_prefix}${String(this.triagemChamada.idtriagem).padStart(3, "0")}`

      this.sendAlertaCliente(this.triagemChamada.idcliente, balcao)

      if (Notification.permission === "granted") {
        const notification = new Notification("SUS Notifications", {
          body: `${triagemSenha} Sua vez chegou, por favor, dirija-se ao balco ${this.bLivre}`,
          // Outras opes de configurao da notificao
        });
      }

    },
    async salvaTriagem() {
      // console.log(this.triagemSelecionada);

      try {
        const response = await http.patch(this.BASE_URL + "triagem", {
          triagem_id: this.triagemSelecionada.idtriagem,
          urgencia: this.triagemSelecionada.codurg,
        });

        this.$router.go(0);
      } catch (error) {
        // console.log({ error });
      }
    },
    sOpcao(Value) {
      if (Value) {
        this.triagemSelecionada.codurg = Value;
      }
    },
    carregaTriagem(Value) {
      // console.log(Value);
      this.triagemSelecionada = Value;
      this.selectedPriority = this.triagemSelecionada.codurg;
    },
    async getTriagem() {
      try {
        const response = await http.post(this.BASE_URL + "triagem", {});

        const rTriagem = response.data.response.triagem;

        this.lTriagem = rTriagem;
      } catch (error) {
        // console.log({ error });
      }
    },
    waitChamada() {
      this.ioclient.on("send_ClientAlert", (mensagem) => {
        // console.log({ ServidorMandou: mensagem })
      });
    },
    waitConnected() {
      this.ioclient.on("connected", (mensagem) => {
        this.returnServerMsg.push(mensagem);
        this.getRooms();
        this.getClients();
        this.sendJoin(1, "Sala de Espera");        
      });
    },
    waitClientJoin() {
      this.ioclient.on("ClientJoin", (mensagem) => {
        // console.log({mensagem})
        this.$router.go(0)       
      });
    },
    getClients() {
      // socket.emit('get_clients');

      this.ioclient.on("clients_list", (clients) => {
        // console.log("Connected clients:", clients);
        this.pacientes = clients
      });
    },
    getRooms() {

      this.ioclient.on("rooms_list", (list) => {
        // console.log("Rooms:", list);
        this.salas = list
      });

    },
    rolarScroll() {
      // setTimeout(() => {
      //   const messageContainer = this.$refs.messageContainer;
      //   messageContainer.scrollTop = messageContainer.scrollHeight;
      // }, 0);
    },
    socketConnection() {
      this.ioclient.on("send_message", (mensagem) => {
        this.returnServerMsg.push(mensagem);
        // this.rolarScroll();
      });
    },
    sendJoin(userId, room) {
      // Evento para se juntar a uma sala
      this.ioclient.emit("join", { userId, room });
    },
    sendText() {
      this.ioclient.emit("send_message", { name: "usurio", message: this.messageText });
      this.messageText = null;
    },
    sendHello() {
      this.ioclient.emit("send_message", { name: "usurio", message: "Ol, servidor!" });
      this.ioclient.emit("send_message", { name: "usurio", message: "Ol, sala!" });
    },
    sendAlertaCliente(id, balcao) {
      this.ioclient.emit("send_ClientAlert", { id, balcao });
    },
  },
};
</script>

<style scoped>
.triagemContainer {
  background-color: rgb(193, 209, 255);
  height: calc(100% - 24px);
  display: flex;
  padding: 12px;
}

.fila {
  /* background-color: brown; */
  width: 360px;
  min-width: 360px;
  height: 100%;
  margin-right: 16px;
  border-radius: 8px;
  overflow: auto;
}

.panelControl {
  background-color: rgba(240, 248, 255, 0.575);
  height: calc(100% - 12px);
  width: 100%;
  border-radius: 8px;
  padding: 6px;
  box-shadow: 1px 1px 1px rgb(51, 51, 51);
}

.pcTop {
  background-color: aliceblue;
  width: 100%;
  min-height: 135px;
  margin-bottom: 4px;
  border-radius: 6px;
  box-shadow: 1px 1px 1px rgb(51, 51, 51);
}

.pcWherever {
  background-color: aliceblue;
  width: 100%;
  height: calc(55% - 4px);
  max-height: calc(55% - 4px);
  border-radius: 6px;
  box-shadow: 1px 1px 1px rgb(51, 51, 51);
  cursor: pointer;
}

.listaTriagem {
  background-color: aliceblue;
  width: calc(100% - 16px);
  min-height: 90px;
  margin-bottom: 8px;
  padding: 6px;
  border-radius: 8px;
  font-size: 12px;
  font-weight: 700;
  color: rgb(73, 73, 73);
  cursor: pointer;
  transition: 0.2s;
  box-shadow: 1px 1px 1px rgb(51, 51, 51);
}

.listaTriagem:hover {
  background-color: rgb(177, 218, 255);
}

.cNome,
.cDocs,
.cContato,
.cMotivo {
  text-align: left;
}

.cNome,
.cDocs,
.cContato,
.cMotivo {
  font-size: 0.8rem;
}

.cDoc,
.cID {
  margin-bottom: 4px;
}

.cNome,
.cSenha,
.cDocs,
.cContato {
  display: flex;
}

.cSenha {
  height: 30px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.1rem;
}

.csPriority3 {
  background-color: rgb(140, 129, 201);
}

.csPriority2 {
  background-color: rgb(201, 187, 0);
}

.csPriority1 {
  background-color: rgb(42, 131, 1);
}

.sPrefix {
  color: rgb(39, 39, 156);
}

.sNumber {
  color: aliceblue;
}

.ltSenha {
  margin-top: 12px;
  margin-bottom: 3px;
}

.dataLabel {
  color: rgb(0, 0, 41);
  margin-right: 10px;
}

.ltSelecionada {
  background-color: aliceblue;
  margin: 0;
  box-shadow: none;
  width: calc(100% - 12px);
  height: calc(100% - 12px);
  cursor: default;
  min-height: 0;
}

.ltSelecionada:hover {
  background-color: aliceblue;
}

.cSenhaSelect {
  height: 100%;
}

.ltSenhaSelect {
  height: 40px;
}

.pcTopMenu {
  height: 60px;
  padding: 6px;
  display: flex;
}

.pSelect {
  text-align: center;
  cursor: pointer;
  height: 30px;
  width: 100%;
  border-radius: 4px;
  font-size: 18px;
  font-weight: 600;
}

.pSelectOption {
  text-align: center;
  /* Alinhar o texto das opes  esquerda */
  margin: 0 auto;
  /* Centralizar as opes dentro do select box */
  cursor: pointer;
  height: 30px;
  font-size: 16px;
}

.priorityChange {
  width: 200px;
  text-align: left;
  margin-right: 16px;
}

.priorityChange div {
  margin-bottom: 4px;
  color: rgb(0, 0, 41);
  font-weight: 600;
}

.salvaTriagem,
.cancelaTriagem,
.chamaTriagem {
  background-color: rgb(13, 6, 54);
  width: 200px;
  margin-top: 24px;
  margin-bottom: 5px;
  color: aliceblue;
  font-weight: 600;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 1px 1px 1px rgb(51, 51, 51);
  cursor: pointer;
  transition: 0.1s;
  margin-right: 16px;
}

.salvaTriagem:hover,
.cancelaTriagem:hover,
.chamaTriagem:hover {
  background-color: rgb(218, 213, 247);
  color: rgb(5, 0, 34);
}

.sChamada,
.csChamada {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 8rem;
  font-weight: 800;
}

.csChamada {
  flex-direction: column;
  height: 100%;
}

.csNumber {
  color: rgb(87, 87, 87);
}

.mensagemChamada {
  color: rgb(0, 0, 34);
  font-size: 3rem;
}
</style>
